@import url(https://fonts.googleapis.com/css?family=Orbitron&text=0123456789:AMP);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  background-image: url("https://i.imgur.com/yd2Khpv.jpg");
  background-position: center;
  background-repeat: repeat-y;
  background-size: cover; }

.contain {
  margin: 25vh 25vw; }

h1.page-title {
  color: black;
  font-family: "Ubuntu", sans-serif;
  font-size: 10vh;
  text-align: center;
  font-weight: 900;
  margin-top: -10vh; }

#reactor {
  margin-top: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around; }

#content {
  display: grid;
  grid-gap: 10vh 10vw;
  grid-template-areas: "left-label right-label" "left-length right-length" "session session"; }

.left-panel, .right-panel {
  background-color: orange;
  border-radius: 5px;
  border: 2px solid black;
  padding: 5px; }

h2#break-label, h2#session-label, h2#timer-label {
  font-family: "Ubuntu", sans-serif;
  font-weight: 700; }

h2#break-label {
  grid-area: left-label; }

h2#session-label {
  grid-area: right-label; }

.controls-left {
  grid-area: left-length;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 7vh;
  font-weight: 700; }

.controls-right {
  grid-area: right-length;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 7vh;
  font-weight: 700; }

.fa, .fas {
  font-size: 5vh; }

.bottom-center-panel {
  grid-area: session;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: orange;
  border-radius: 5px;
  border: 2px solid black;
  padding: 5px; }

#time-left {
  border-radius: 50%;
  border: 3px solid black;
  padding: 20px;
  font-family: "Orbitron", sans-serif;
  font-size: 25px;
  font-weight: 900;
  background-color: black;
  color: white; }

.time-controls {
  padding: 5px; }

i#start_stop, i#reset {
  padding: 5px; }

.yellow-text {
  color: yellow !important; }

.white-text {
  color: white; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

